/* общие компоненты для всех страниц */
.apply-btn {
  display: inline-block;
  text-decoration: none;
  border: 0;
  text-align: center;
  color: #fff;
  background-color: #418ad3;
}
.apply-btn:hover {
  box-shadow: 0 0 0 99px rgba(255,255,255,0.1) inset;
}
.apply-btn:active {
  box-shadow: 0 0 0 99px rgba(0,0,0,0.2) inset;
  transition: none;
}
.logo {
  width: 446px;
  height: 228px;
}
@media (max-width: 1220px) {
  .logo__text {
    display: none;
  }
}
.menu-toggle {
  width: 50px;
  height: 24px;
  border: 0;
  background: 0;
  color: #418ad3;
  cursor: pointer;
  outline: 0;
}
.menu-toggle__line {
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background: currentColor;
  transition: 0.3s;
  box-shadow: 0 0 1px transparent;
}
.menu-toggle__line_top {
  top: 0;
  width: 40px;
}
.menu-toggle__line_bottom {
  top: 100%;
  width: 40px;
}
.menu-toggle__line_middle {
  top: 50%;
  margin-top: -1px;
}
.menu-toggle:hover .menu-toggle__line {
  width: 100%;
}
.menu-toggle_active .menu-toggle__line_middle {
  opacity: 0;
  transform: translateX(100%);
}
.menu-toggle_active .menu-toggle__line_top,
.menu-toggle_active .menu-toggle__line_bottom {
  top: 50%;
  margin-top: -1px;
  width: 100%;
}
.menu-toggle_active .menu-toggle__line_top {
  transform: rotate(45deg);
}
.menu-toggle_active .menu-toggle__line_bottom {
  transform: rotate(-45deg);
}
/* элементы отдельных страниц и структурные элементы */
.container {
  margin-left: auto;
  margin-right: auto;
  max-width: 1420px;
  padding-left: 40px;
  padding-right: 40px;
}
@media (max-width: 720px) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }
}
.inner {
  margin-left: auto;
  margin-right: auto;
  max-width: 1180px;
}
@media (max-width: 1080px) {
  .inner {
    max-width: none;
  }
}
.user-menu {
  margin: 0;
  padding: 0;
  list-style: none;
  height: 50px;
  line-height: 50px;
  font-size: 20px;
  color: #418ad3;
}
.user-menu::after {
  content: "";
  display: table;
  clear: both;
}
@media (max-width: 1220px) {
  .user-menu {
    visibility: hidden;
  }
}
.user-menu__item {
  margin-left: 36px;
  float: left;
}
.user-menu__item_signin {
  position: relative;
  display: none;
  line-height: 1;
}
.user-menu__item_signin .user-menu__link::before {
  content: "";
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.user-menu__item_signin svg {
  margin: 0 auto 3px;
  display: block;
  fill: currentColor;
}
@media (max-width: 1220px) {
  .user-menu__item_signin {
    position: absolute;
    right: 0;
    top: 20px;
    visibility: visible;
    display: block;
  }
}
.user-menu__link {
  position: relative;
  color: inherit;
  text-decoration: none;
  transition: 0.2s;
}
.user-menu__link::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: 2px;
  opacity: 1;
  border-top: 1px solid #c1d7ed;
  transition: 0.2s;
}
.user-menu__link:hover::after {
  opacity: 0;
}
.user-menu__apply {
  width: 220px;
}
.main-menu {
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-transform: uppercase;
}
.main-menu__item {
  margin-left: 70px;
  font-size: 25px;
  float: left;
}
@media (max-width: 1490px) {
  .main-menu__item {
    margin-left: 40px;
    font-size: 20px;
  }
}
@media (max-width: 1220px) {
  .main-menu__item {
    margin: 0;
    display: block;
    float: none;
  }
}
.main-menu__link {
  position: relative;
  color: #372d02;
  font-family: "Proxima Nova", sans-serif;
  text-decoration: none;
}
.main-menu__link::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  border-top: 1px solid;
  transition: 0.2s;
}
.main-menu__link:hover::after {
  opacity: 1;
}
@media (max-width: 1220px) {
  .main-menu__link {
    display: block;
    padding: 20px;
  }
  .main-menu__link::after {
    content: none;
  }
  .main-menu__link:hover {
    color: #fff;
    background: #418ad3;
  }
}
.page-header {
  position: absolute;
  z-index: 3;
  left: 10px;
  right: 10px;
  top: 30px;
  margin-left: auto;
  margin-right: auto;
  box-sizing: border-box;
  padding: 0 10px 22px;
  max-width: 90%;
  max-width: calc(100% - 20px);
  width: 1540px;
  background-color: #e3eef6;
  color: #372d02;
  border-radius: 10px;
  box-shadow: 2px 17px 36.34px 9.66px rgba(0,0,0,0.31);
}
.page-header__inner {
  position: relative;
}
.page-header__inner::after {
  content: "";
  display: table;
  clear: both;
}
.page-header__toggle {
  position: absolute;
  top: 28px;
  left: 0;
  margin: auto;
  display: none;
}
.page-header__logo {
  position: absolute;
  left: 0px;
  bottom: 4px;
  width: 259px;
  height: 133px;
}
.page-header__logo svg {
  width: 100%;
  height: 100%;
}
.page-header__user-menu,
.page-header__main-menu {
  float: right;
  clear: right;
}
.page-header__user-menu {
  margin-bottom: 42px;
}
@media (max-width: 1220px) {
  .page-header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    padding: 0;
    width: auto;
    max-width: none;
    border-radius: 0;
  }
  .page-header__inner {
    height: 80px;
    overflow: hidden;
    transition: 0.3s;
  }
  .page-header__toggle {
    display: block;
  }
  .page-header__logo {
    left: 0;
    right: 0;
    top: -8px;
    bottom: auto;
    margin: auto;
    width: 170px;
  }
  .page-header__main-menu {
    visibility: hidden;
    opacity: 0;
    float: none;
    padding-bottom: 20px;
    background: #e3eef6;
  }
  .page-header_menu-open .page-header__main-menu {
    visibility: visible;
    opacity: 1;
  }
}
.lang-select {
  position: relative;
  cursor: pointer;
}
.lang-select__current {
  display: inline;
}
.lang-select__current::after {
  content: none;
}
.lang-select__icon {
  margin-right: 4px;
  width: 18px;
  height: 14px;
  border: 1px solid #c3c3c3;
}
.lang-select__text {
  position: relative;
}
.lang-select__text::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: 2px;
  opacity: 0.7;
  border-top: 1px dashed;
  transition: 0.2s;
}
.lang-select__list {
  position: absolute;
  z-index: 1;
  left: 50%;
  transform: translateX(-50%);
  opacity: 0;
  visibility: hidden;
  min-width: 200%;
  background-color: #fff;
  box-shadow: 0 3px 5px rgba(0,0,0,0.2);
}
.lang-select__item {
  display: block;
  padding: 0 18px;
  color: #418ad3;
  text-decoration: none;
  transition: 0.2s;
}
.lang-select__item:hover {
  background: rgba(65,138,211,0.1);
}
.lang-select:hover .lang-select__list,
.lang-select_open .lang-select__list {
  opacity: 1;
  visibility: visible;
  transition: 0.3s;
}
.splash {
  position: relative;
  overflow: hidden;
  background-color: #b2deff;
}
.splash::before {
  content: "";
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.2);
}
.splash__inner {
  margin-left: auto;
  margin-right: auto;
  max-width: 1180px;
}
@media (max-width: 1080px) {
  .splash__inner {
    max-width: none;
  }
}
.splash__image {
  position: absolute;
  bottom: 0;
}
.splash__image_bg {
  left: 0;
  right: 0;
  margin: 0 auto;
}
.splash__image_side {
  z-index: 1;
  left: 0;
}
.splash__image_flag {
  z-index: 1;
  right: 0;
}
.splash-content {
  position: relative;
  z-index: 2;
  display: table;
  width: 100%;
  height: 100vh;
  min-height: 100vh;
  border-collapse: collapse;
}
@media (max-width: 720px) {
  .splash-content {
    height: auto;
    min-height: 0;
  }
}
.splash-content__inner {
  display: table-cell;
  vertical-align: middle;
  padding: 180px 0;
}
@media (max-width: 720px) {
  .splash-content__inner {
    padding: 120px 0 100px;
  }
}
.splash-title {
  font-size: 50px;
  font-weight: normal;
  text-shadow: 7px 4px 7.9px rgba(0,0,0,0.18);
}
.splash-title__accent {
  margin-bottom: 37px;
  display: block;
  font: 900 normal 300%/0.86 "Proxima Nova", "PT Sans", sans-serif;
}
@media (max-width: 1080px) {
  .splash-title {
    font-size: 40px;
  }
  .splash-title__accent {
    font-size: 100px;
  }
}
@media (max-width: 720px) {
  .splash-title {
    font-size: 26px;
    line-height: 1.34;
  }
  .splash-title__accent {
    margin-bottom: 10px;
    font-size: 46px;
  }
}
.features {
  padding: 0 26px;
  overflow: hidden;
  background-color: #008bd7;
}
.ctx-page-loaded .features {
  background-image: url("../img/pattern.png");
}
@media only screen and (-webkit-min-device-pixel-ratio: 1.5), only screen and (min-resolution: 144dpi) {
  .ctx-page-loaded .features {
    background-image: url("../img/pattern@2x.png");
    background-size: 100px 100px;
  }
}
.feature-grid {
  margin-left: -26px;
  margin-right: -26px;
  padding: 92px 0;
  font-size: 0;
}
@media (max-width: 1080px) {
  .feature-grid {
    padding-top: 42px;
    padding-bottom: 42px;
  }
}
@media (max-width: 860px) {
  .feature-grid {
    padding-top: 32px;
    padding-bottom: 32px;
  }
}
.feature {
  margin-bottom: 49px;
  box-sizing: border-box;
  width: 25%;
  display: inline-block;
  vertical-align: top;
  padding: 0 26px;
  font-size: 25px;
}
.feature__accent {
  display: block;
  font-size: 35px;
  text-transform: uppercase;
}
.feature_1,
.feature_2 {
  width: 50%;
  vertical-align: middle;
}
.feature_1 .feature__accent {
  font-size: 65px;
}
.feature_2 {
  font-size: 40px;
}
.feature_2 .feature__accent {
  font-size: 50px;
}
@media (max-width: 1080px) {
  .feature {
    width: 50%;
  }
  .feature_1,
  .feature_2 {
